import { ExchangeCode } from "../hooks/apicalls";
import { ElementContextData } from "../context/DataContext";
import { ElementContextRoute } from "../context/RouteContext";
import { useContext, useRef, useState } from "react";
import "../styles/Codes.css";
import SamsConfetti from "../components/SamsConfetti";
import { ElementContextPopUp } from "../context/PopUpContext";
function Codes() {
  const { deleteSavedItems, changeRoute, getCurrentToken } = useContext(ElementContextRoute);
  const { SetUserData, UserData, setNewUserDiamonds } = useContext(ElementContextData);
  const { changePopUpLoading } = useContext(ElementContextPopUp);
  const [popUpResponse, setPopUpResponse] = useState("");
  const [inputValue, setInputValue] = useState('');

  let redeemedDiamonds = useRef(0);
  let rewardErrorPopUpTitle = useRef("");
  let rewardErrorPopUpContent = useRef("");
  let rewardPopUpContent = <></>;

  const handleOnChangeInput = (input) => {
    setInputValue(input);
  }

  const codeNotFoundPopUp = () => {
    rewardErrorPopUpTitle.current = "Lo sentimos, no existe el cdigo que escribiste.";
    rewardErrorPopUpContent.current = "An tenemos muchsimos cdigos para ti, pendiente en nuestras redes sociales.";
    setPopUpResponse("Error");
  }

  const expiredCodePopUp = () => {
    rewardErrorPopUpTitle.current = "Lo sentimos, este cdigo ha expirado.";
    rewardErrorPopUpContent.current = "An tenemos muchsimos cdigos para ti, pendiente en nuestras redes sociales.";
    setPopUpResponse("Error");
  }

  const maxRedemptionsReachedPopUp = () => {
    rewardErrorPopUpTitle.current = "Lo sentimos, se han agotado los canjes permitidos para este cdigo.";
    rewardErrorPopUpContent.current = "An tenemos muchsimos cdigos para ti, pendiente en nuestras redes sociales.";
    setPopUpResponse("Error");
  }

  const noStockErrorPopUp = () => {
    rewardErrorPopUpTitle.current = "Lo sentimos, se han agotado los canjes permitidos para este cdigo.";
    rewardErrorPopUpContent.current = "An tenemos muchsimos cdigos para ti, pendiente en nuestras redes sociales.";
    setPopUpResponse("Error");
  }

  const openGeneralErrorPopUp = () => {
    rewardErrorPopUpTitle.current = "Lo sentimos, ha ocurrido un error, favor de intentar ms tarde.";
    rewardErrorPopUpContent.current = "An tenemos muchsimos premios para ti.";
    setPopUpResponse("Error");
  }

  const handleRewardPopUpClose = () => {
    redeemedDiamonds.current = 0;
    setInputValue("");
    setPopUpResponse(null);
  }

  const handleLogOut = async () => {
    SetUserData(null);
    await deleteSavedItems();
    changeRoute("Login");
  }

  const handleExchangeCode = async () => {
    if (inputValue === "") return;

    const token = await getCurrentToken();

    if(token === null || token === "") {
      await handleLogOut();
      return;
    }
    changePopUpLoading(true)

    const response = await ExchangeCode(`Bearer ${token}`, inputValue);
    const data = await response.json();
    if (response.ok) {
        redeemedDiamonds.current = data.user.related.diamonds - UserData.current.user.related.diamonds;
        setNewUserDiamonds(data.user.related.diamonds);
        setPopUpResponse("Success");
        setInputValue("");
        changePopUpLoading(false)
      } else {
        if (data.message) {
          switch(data.message) {
            case "api.error.unauthorized":
              await handleLogOut();
              break;
            case "api.error.code_not_found":
              codeNotFoundPopUp();
              break;
            case "api.error.code_expired":
              expiredCodePopUp();
              break;
            case " api.error.max_redemptions_reached":
              maxRedemptionsReachedPopUp();
              break;
            case " api.error.no_stock":
              noStockErrorPopUp();
              break;
            default:
              openGeneralErrorPopUp();
              break;
          }
        }else{
          openGeneralErrorPopUp();
        }
        changePopUpLoading(false)
        return;
      }
     
  }

  if(popUpResponse === "Error"){
    rewardPopUpContent = (
      <div className="PopUp">
        <div style={{ height: "auto" }} className="PopUpDialog">
          <div className="GeneralButtonContainer">
            <p style={{ marginTop: "30px", }} className="subTitlePopUpReward">
              {rewardErrorPopUpTitle.current}
            </p>

            <p
              style={{ fontWeight: "400", margin: "0px", marginBottom: "20px" }}
              className="subTitlePopUpReward"
            >
              {rewardErrorPopUpContent.current}
            </p>

            <button className="GeneralButton4" onClick={handleRewardPopUpClose}>
              Aceptar
            </button>

            <div style={{ height: "30px" }}></div>
          </div>
        </div>
      </div>
    );
  }

  if(popUpResponse === "Success"){
    rewardPopUpContent = (
      <>
        <SamsConfetti></SamsConfetti>
        <div className="PopUp">
          <div style={{ height: "auto", }} className="PopUpDialog">
            <div className="GeneralButtonContainer">

              <p className="subTitlePopUpReward">
                Cdigo canjeado con exito!
              </p>

              <p
                style={{
                  fontWeight: "400",
                  margin: "0px",
                  marginBottom: "14px",
                  color: "#0066A1",
                  fontSize: "32px"
                }}
                className="subTitlePopUpReward"
              >
                {`${redeemedDiamonds.current} diamantes`}
              </p>

              <p
                style={{
                  fontWeight: "400",
                  margin: "0px",
                  marginBottom: "14px",
                }}
                className="subTitlePopUpReward"
              >
                T cdigo ha sido redimido correctamente.
                Disfruta de tus recompensas y gracias por ser parte de nuestra comunidad
                de Sam's Lover.
              </p>

              <button
                style={{}}
                className="GeneralButton4"
                onClick={handleRewardPopUpClose}
              >
                Aceptar
              </button>

              <div style={{ height: "30px" }}></div>
            </div>
          </div>
        </div>
      </>
    );
  }


  return (
    <>
    <>{rewardPopUpContent}</>
      <div className="CodePageContainer">
        <div className="headerSpacer"></div>
        <p className="CodeTitle">Redime tu cdigo</p>
        <p className="CodeText">
          Obten diamantes gratis con los cdigos Sam's Lovers Ya tienes un
          cdigo? Redimelo aqu
        </p>
        <div style={{opacity: "0.3", width: "90%", maxWidth: "1000px"}} className="Divider"></div>
        <div className="CodeInputContainer" style={{maxWidth: "700px"}}>

            <p className="CodeInputText"> Introduce tu cdigo</p>
          <input style={{width: "auto"}} placeholder="Cdigo"  className="GeneralInput" value={inputValue} onChange={e => handleOnChangeInput(e.target.value)}></input>
        </div>
        <div style={{width: "70%", paddingTop: "10px", maxWidth: "600px"}} className="CodeButtonContainer">
          <button style={{fontSize: "14px"}} className="GeneralButton4" onClick={handleExchangeCode}>Canjear cdigo</button>
        </div>
      </div>
    </>
  );
}

export default Codes;
